import React, { useState } from 'react';
import {useNavigate} from 'react-router-dom';

function Contact() {
  let navigate = useNavigate();
  
  const [owner, newowner] = useState[(
  {id: 1, Name:"Tamil" },
  {id: 3, Name:"Ravi" },
  {id: 4, Name:"Vara"})];

 const removeHandler = (id) =>{
 const newdata = owner.filter(owner => owner.id!==id)
 newowner(newdata);
    
  }

  return (
  <div class="container p-3">
    <h1>Contact</h1>
    <button class="btn" onClick={()=>{navigate("/")}}>Home</button>

    {owner.map((owner)=>(
     
      <div>
       <p>{owner.id} . {owner.Name} {""}</p>  
      <button  onClick={()=>removeHandler(owner.id)}>Delete</button> 
      </div>
      
    ))}

    </div>
  );
  
}

export default Contact;