import React, {useState} from 'react';
import {useNavigate, useParams} from 'react-router-dom';

function About() {
    let navigate= useNavigate();
    const [count, newCount]= useState(0);
  return (
    <div>
<div class='container-fluid bg-black'>
      <div class="row p-5 align-items-center  justify-content-left">

      <div class='col-md-6 col-lg-6 '>
        <img src="Ravi-image.JPG" width="50%" height="90%"/>
      </div>

      <div class='col-md-6 '>
   
      <h1 style={{color:"white"}}>About </h1>
         
   

        <h1 style={{color:"white"}}>I Am Ravi Shankar</h1>
        <p style={{color:"#cfd1d0"}}>Front-End Developer</p>
        <div class="progress" >
        <div class="progress-bar progress-bar-striped progress-bar-animated" style={{width:"90%"}}>90% HTML</div>
        </div>
        <button class="btn btn-primary m-3" onClick={()=>navigate("/")}>Home</button>
        <p style={{color:"white"}}>The count is going {count}</p>
        <button class="btn btn-primary m-3" onClick={()=>newCount(count+1)}>Increase</button>
      </div >

      </div>
    </div>

    <div class="container-fluid bg-primary">
      <div class="row">
        <div class="col-lg-8 justify-content-center">
          <h2 >Skills</h2>
        </div>
      </div>
    </div>
    </div>
   
  )
}

export default About;