import React, { useState } from 'react'
import{useNavigate} from 'react-router-dom';

function Home() {
var navigate= useNavigate();
const [count, newcount]=useState(0);
  return (
    <div class='container-fluid bg-black'>
      <div class="row p-5 align-items-center">

      <div class='col-md-6 col-lg-6 '>
        <img src="Ravi-image.JPG" width="50%" height="90%"/>
      </div>

      <div class='col-md-6 '>
        <h1 style={{color:"white"}}>I Am Ravi Shankar</h1>
        <p style={{color:"#cfd1d0"}}>Front-End Developer</p>
        <button class="btn btn-primary" onClick={()=>navigate("/about")}>About Me</button>
        <h3 style={{color:'white'}}>{count}</h3>
        <button onClick={()=>newcount(count+1)}>next</button>

      </div>

      </div>
    </div>
  )
}

export default Home;